.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 30px;
}

ul,
ol {
  list-style: none;
}
ul,
ol,
h1,
h2,
p {
  margin: 0;
  padding: 0;
}

.main-title {
  margin-bottom: 40px;
}
.title {
  margin-bottom: 30px;
}
button {
  cursor: pointer;

  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background-color: rgb(127, 189, 224);
  border: 2px solid rgb(127, 189, 224);
}
button:hover,
button:focus {
  background-color: #fff;
}
input {
  height: 23px;
  border-radius: 5px;
  border: 2px solid rgb(127, 189, 224);
}
